import React, { useEffect, useState } from 'react';
import { listVideos } from 'serverAPI';
import Thumbnail from './Thumbnail';
import 'styles/home.css';

function Home() {
  // TODO: 20  
  // TODO: onScroll    
  const [load, setLoad] = useState(false);
  const [thumbnails, setThumbnails] = useState([]);

  useEffect(() => {
    async function loadThumbnails() {
      // You can await here
      const vidoeList = await listVideos();
      const {
        content,
        pageable,
        totalPages,
        totalElements,
        last,
        number,
        sort,
        size,
        numberOfElements,
        first,
        empty,
      } = vidoeList;
      const thumbnailssss = content.map(info => <Thumbnail key={info.videoId} info={info} />);
      setThumbnails(thumbnailssss);
      // ...
      setLoad(true);
    }
    loadThumbnails();

    if (thumbnails.length === 0) {
      thumbnails.push('   .');
    }
  }, []);

  return (
    <div className="home_background">
      <div id="banner">
        <div id="banner_img" />
      </div>
      <div className="video_list_title"> </div>
      <div className="tag_container">
        <div className="tag">
          <span>Education</span>
        </div>
        <div className="tag">
          <span>Movie</span>
        </div>
        <div className="tag">
          <span>Animation</span>
        </div>
        <div className="tag">
          <span>Life</span>
        </div>
        <div className="tag">
          <span>Landscape</span>
        </div>
      </div>
      <div className="video_list">{load ? thumbnails : ' .'}</div>
    </div>
  );
}
export default Home;
