import React, { useState } from 'react';
import axios from 'axios';
import config from '../config.json';

function VideoUpload() {
  const { SERVER_ADDRESS } = config;
  const [title, setTitle] = useState('');
  const [description, setDescription] = useState('');
  const [selectedVideo, setSelectedVideo] = useState(null);
  const [selectedImage, setSelectedImage] = useState(null);

  const [uploadFlag, setUploadFlag] = useState(0);
  const form = new FormData();

  const submitVideo = e => {
    e.preventDefault();
    const json = JSON.stringify({
      title,
      description,
      exposure: 'public',
    });
    const blob = new Blob([json], { type: 'application/json' });
    form.append('body', blob);
    form.append('video', selectedVideo);
    form.append('thumbnail', selectedImage);
    axios
      .post(`${SERVER_ADDRESS}/videos/`, form, {
        headers: {
          Authorization: sessionStorage.getItem('authToken'),
          'Content-Type': 'multipart/form-data',
          Accept: 'application/json, application/xml, text/plain, text/html, */*',
        },
      })
      .then(response => {
        setUploadFlag(1);
      })
      .catch(response => {
        setUploadFlag(-1);
      });
  };

  if (uploadFlag === 0) {
    return (
      <form onSubmit={submitVideo}>
        <input type="file" onChange={e => setSelectedVideo(e.target.files[0])} />
        <input type="file" onChange={e => setSelectedImage(e.target.files[0])} />
        <input type="text" placeholder="" onChange={e => setTitle(e.target.value)} />
        <input type="text" placeholder=" " onChange={e => setDescription(e.target.value)} />

        <button type="submit"></button>
      </form>
    );
  }
  if (uploadFlag === 1) {
    return (
      <div>
        <h3> .</h3>
        <h6>   (  )</h6>
      </div>
    );
  } // uploadFlag == -1
  return <h1> .</h1>;
}

export default VideoUpload;
